.app {
  padding: 10px;
}

form {
  width: 100%;
}
.form-input {
  margin: 10px 0px;
}

.blog-post-display {
  border: 2px solid rgb(25, 96, 119);
  margin: 5px 0px;
  border-radius: 4px;
  padding: 10px;
  width: 60%;
}

button {
  background-color: rgb(68, 13, 170);
  font: Tahoma;
  color: black;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  line-height: 1.5;
  border-radius: 0.25rem;
  cursor: pointer;
}

.form-input input,
.form-input textarea {
  width: 30%;
  border-radius: 2px;
  border-color: rgb(25, 96, 119);
  border-radius: 0.25rem;
  font-size: 1rem;
  padding: 0.375rem 0.75rem;
  font: Tahoma;
  font-weight: bold;
}

.title {
  text-align: center;
}
